<template><section>

<h1>w-icon图标</h1>
<hr>
<h3>基础用法</h3>
<p>使用<code v-pre class="code_inline">type</code>、<code v-pre class="code_inline">size</code>和<code v-pre class="code_inline">color</code>属性来定义 Icon 的样式。</p>
<h3>其中type有三个便捷的尺寸定义参数<code v-pre class="code_inline">s(表示small)</code>、<code v-pre class="code_inline">m(表示middle)</code>、<code v-pre class="code_inline">l(表示large)</code>。</h3>
<h3>代码示例</h3>
<pre v-pre><code v-pre>&lt;w-icon size='s' type=&quot;icon-close&quot;&gt;&lt;/w-icon&gt;  小
&lt;w-icon size='m' type=&quot;icon-close&quot;&gt;&lt;/w-icon&gt;  中
&lt;w-icon size='l' type=&quot;icon-close&quot;&gt;&lt;/w-icon&gt;  大
</code></pre>
<div class="demo-block demo-icon">
  <div>
    <w-icon size="s" type="icon-close"></w-icon>
    <w-icon size="m" type="icon-close"></w-icon>
    <w-icon size="l" type="icon-close"></w-icon>
    <w-icon size="16" type="icon-close"></w-icon>
    <w-icon size="18" type="icon-close"></w-icon>
    <w-icon size="24" type="icon-camera2"></w-icon>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <w-icon size="s" type="icon-close"></w-icon>
  <w-icon size="m" type="icon-close"></w-icon>
  <w-icon size="l" type="icon-close"></w-icon>
  <w-icon size="16" type="icon-close"></w-icon>
  <w-icon size="18" type="icon-close"></w-icon>
  <w-icon size="24" type="icon-camera2"></w-icon>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">'s'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">'m'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">'l'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">16</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"18"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-icon</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"24"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"icon-camera2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-icon</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>尺寸</td>
<td>String,number</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>类型</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>All icons</h3>
<div class="iconBox">
  <div class="iconItem" v-for="(item, index) in iconList">
    <i :class="item" :style="{color:RandomColor()}"></i>
    <p>{{item}}</p>
  </div>
</div>
</section></template>
<style lang="scss">
.demo-icon{
  margin-top:10px;
  i{
    margin-right:15px
  }
}
.iconBox{
  width: 100%;
  height: auto;
  margin: 0 auto;
  .iconItem{
    height: 64px;
    width: 120px;
    padding: 10px;
    margin:5px;
    float: left;
    i{
      font-size: 24px;
    }
    p{
      font-size: 10px;
    }
  }
}
</style>
<script>
import iconList from '@/assets/js/icon.json'
 export default {
    data(){
      return{
        iconList:iconList
      }
    },
    computed:{
      RandomColor() {
        return function(){
          let r, g, b;
          r = Math.floor(Math.random() * 256);
          g = Math.floor(Math.random() * 256);
          b = Math.floor(Math.random() * 256);
          return "rgb(" +r + ',' +g+ ',' +b+ ")";
        }
      }
    }
 }
</script>


