<template><section>

<h1>Modal 对话框</h1>
<hr>
<p>用于页面中展示重要的提示信息。</p>
<h3>基本用法</h3>
<p>页面中的非浮层元素，不会自动消失。</p>
<div class="dome-modal demo-block">
  <w-button @click="showModal=!showModal">显示弹框</w-button>
  <w-button @click="showModal1=!showModal1">自由定制内容</w-button>
<p><W-Modal v-model="showModal" title="提示" @on-ok="clickOk">
<p>啦啦啦啦</p>
<p>对p标签默认居中</p>
</W-Modal></p>
<p><W-Modal v-model="showModal1" title="可关闭的弹出层" width="800" :closable="true" :footer-hide="true" @on-ok="clickOk">
<div style="height:300px;">
<h1>制定你想显示的内容</h1>
</div>
</W-Modal></p>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <w-button @click="showModal=!showModal">显示弹框</w-button>
  <w-button @click="showModal1=!showModal1">自由定制内容</w-button>

  <w-modal v-model="showModal" title="提示" @on-ok="clickOk">
    <p>啦啦啦啦</p>
    <p>对p标签默认居中</p>
  </w-modal>

  <w-modal v-model="showModal1" title="我是自定义内容" width="800" closable="true" :footer-hide="true" @on-ok="clickOk">
    <div style="height:300px;">
      <h1>制定你想显示的内容</h1>
    </div>
  </w-modal>
</div>
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showModal=!showModal"</span>&gt;</span>显示弹框<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showModal1=!showModal1"</span>&gt;</span>自由定制内容<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">W-Modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"showModal"</span>
  <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
  <span class="hljs-attr">@on-ok</span>=<span class="hljs-string">"clickOk"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>啦啦啦啦<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对p标签默认居中<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">W-Modal</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">W-Modal</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"showModal1"</span>
  <span class="hljs-attr">title</span>=<span class="hljs-string">"我是自定义内容"</span>
  <span class="hljs-attr">width</span>=<span class="hljs-string">"800"</span>
  <span class="hljs-attr">closable</span>=<span class="hljs-string">true</span>
  <span class="hljs-attr">:footer-hide</span>=<span class="hljs-string">true</span>
  <span class="hljs-attr">@on-ok</span>=<span class="hljs-string">"clickOk"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:300px;"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h1</span>&gt;</span>制定你想显示的内容<span class="hljs-tag">&lt;/<span class="hljs-name">h1</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">W-Modal</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>通过以下形式进行调用</h3>
<pre v-pre><code v-pre class="language-js"><span class="hljs-keyword">this</span>.$Modal.info(config)
<span class="hljs-keyword">this</span>.$Modal.success(config)
<span class="hljs-keyword">this</span>.$Modal.warning(config)
<span class="hljs-keyword">this</span>.$Modal.error(config)
<span class="hljs-keyword">this</span>.$Modal.confirm(config)
</code></pre>
<div class="dome-modal demo-block">
  <w-button type="primary" @click="showConfim('info')">info</w-button>
  <w-button type="success" @click="showConfim('success')">success</w-button>
  <w-button type="warning" @click="showConfim('warning')">warning</w-button>
  <w-button type="danger" @click="showConfim('error')">error</w-button>
  <w-button type="info" @click="showConfim('confirm')">confirm</w-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <w-button @click="showConfim()">显示弹框</w-button>
</div>


</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showConfim()"</span>&gt;</span>显示弹框<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span>{
  <span class="hljs-attr">methods</span>: {
    showConfim(){
      <span class="hljs-keyword">this</span>.$Modal.confirm({
          <span class="hljs-attr">title</span>:<span class="hljs-string">'警告'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'数据删除后不可恢复'</span>,
          <span class="hljs-attr">width</span>:<span class="hljs-string">'300'</span>,
          <span class="hljs-attr">onOk</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
             <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'点击了ok'</span>)
          },
          <span class="hljs-attr">onCancel</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">return</span>
          }
      });
    }
  }
}

</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题，必选参数。也可通过默认 slot 传入</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/error</td>
<td>info</td>
</tr>
<tr>
<td>description</td>
<td>辅助性文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>center</td>
<td>文字是否居中</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>close-text</td>
<td>关闭按钮自定义文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示图标</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>关闭alert时触发的事件</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.demo-modal{
  margin-top: 10px;
}

</style>
<script>
 export default {
    data(){
      return{
        showModal:false,
        showModal1:false
      }
    },
    methods: {
      hello() {
        alert('Hello World!');
      },
      clickOk(){
        alert('clicked Ok')
      },

      showConfim(type){
        const title = 'Title';
        const content = '<p>Content of dialog</p><p>Content of dialog</p>';
        switch (type) {
            case 'info':
                this.$Modal.info({
                    title: title,
                    content: content
                });
                break;
            case 'success':
                this.$Modal.success({
                    title: title,
                    content: content
                });
                break;
            case 'warning':
                this.$Modal.warning({
                    title: title,
                    content: content
                });
                break;
            case 'error':
                this.$Modal.error({
                    title: title,
                    content: content
                });
                break;
            case 'confirm':
            this.$Modal.confirm({
                title:'警告',
                content: '数据删除后不可恢复',
                okText:'保存',
                onOk: () => {
                   this.$Message({content:'你点了保存'})
                },
                onCancel: () => {
                   this.$Message({type:'error',content:'你点了取消'})
                }
            });
        }
      }
    }
  }
</script>


