<template><section>

<h1>Message 全局提示</h1>
<hr>
<h3>基本用法</h3>
<h3>通过以下形式进行调用</h3>
<pre v-pre><code v-pre class="language-js"><span class="hljs-keyword">this</span>.$Message(config)
</code></pre>
<p>页面中的非浮层元素，不会自动消失。</p>
  <div class="dome-message demo-block">
    <w-button type="info" @click="showMessage('info')">显示info</w-button>
    <w-button type="warning" @click="showMessage('warning')">显示warning</w-button>
    <w-button type="danger" @click="showMessage('error')">显示error</w-button>
    <w-button type="success" @click="showMessage('success')">显示success</w-button>
    <w-button @click="showMessage('closable')">可关闭</w-button>
    <w-button @click="showMessage('ten')">显示10秒</w-button>
    <w-button @click="showMessage('unauto')">不自动关闭</w-button>
    <w-button @click="showMessage('closeTxt')">自定义关闭文字</w-button>
    <!-- <w-button  @click="showMessage('isLink')">支持链接</w-button> -->
  </div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <w-button type="info" @click="showMessage('info')">显示info</w-button>
  <w-button type="warning" @click="showMessage('warning')">显示warning</w-button>
  <w-button type="danger" @click="showMessage('error')">显示error</w-button>
  <w-button type="success" @click="showMessage('success')">显示success</w-button>
  <w-button @click="showMessage('closable')">可关闭</w-button>
  <w-button @click="showMessage('ten')">显示10秒</w-button>
  <w-button @click="showMessage('unauto')">不自动关闭</w-button>
  <w-button @click="showMessage('closeTxt')">自定义关闭文字</w-button>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('info')"</span>&gt;</span>显示info<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('warning')"</span>&gt;</span>显示warning<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('error')"</span>&gt;</span>显示error<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('success')"</span>&gt;</span>显示success<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span>  <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('closable')"</span>&gt;</span>可关闭<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span>  <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('ten')"</span>&gt;</span>显示10秒<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span>  <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('unauto')"</span>&gt;</span>不自动关闭<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span>  <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessage('closeTxt')"</span>&gt;</span>自定义关闭文字<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
 <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>:{
      showMessage(type){
        <span class="hljs-keyword">switch</span>(type){
          <span class="hljs-keyword">case</span> <span class="hljs-string">'info'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'info'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is info'</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'warning'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'warning'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is warning'</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'error'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'error'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is error'</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'success'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'success'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is success'</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'closable'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'info'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is closable info'</span>,<span class="hljs-attr">closable</span>:<span class="hljs-literal">true</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'ten'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'info'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is ten minutes info'</span>,<span class="hljs-attr">duration</span>:<span class="hljs-number">10</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'unauto'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'info'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is unauto close info'</span>,<span class="hljs-attr">duration</span>:<span class="hljs-number">0</span>})
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">break</span>;
          <span class="hljs-keyword">case</span> <span class="hljs-string">'closeTxt'</span>:
            <span class="hljs-keyword">this</span>.$Message({<span class="hljs-attr">type</span>:<span class="hljs-string">'info'</span>,<span class="hljs-attr">content</span>:<span class="hljs-string">'this is custom closeTxt info'</span>,<span class="hljs-attr">closeTxt</span>:<span class="hljs-string">'我知道了'</span>,<span class="hljs-attr">duration</span>:<span class="hljs-number">10</span>})
          <span class="hljs-keyword">break</span>;
        }
      }
    }
 }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>


</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>info,success,warning,error</td>
<td>info</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>String</td>
<td>—</td>
<td>--------</td>
</tr>
<tr>
<td>closable</td>
<td>是否可可关闭</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>duration</td>
<td>显示时长</td>
<td>Number</td>
<td>—</td>
<td>3(秒)</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.demo-message{
  margin-top: 10px;
}

</style>
<script>
 export default {
    data(){
      return{
        showModal:false
      }
    },
    methods:{
      showMessage(type){
        switch(type){
          case 'info':
            this.$Message({content:'this is info'})
          break;
          case 'warning':
            this.$Message({type:'warning',content:'this is warning'})
          break;
          case 'error':
            this.$Message({type:'error',content:'this is error'})
          break;
          case 'success':
            this.$Message({type:'success',content:'this is success'})
          break;
          case 'closable':
            this.$Message({type:'info',content:'this is closable info',closable:true})
          break;
          case 'ten':
            this.$Message({type:'info',content:'this is ten minutes info',duration:10})
          break;
          case 'unauto':
            this.$Message({type:'info',content:'this is unauto close info',duration:0})
          break;
          case 'closeTxt':
            this.$Message({type:'info',content:'this is custom closeTxt info',closeTxt:'朕知道了',duration:10})
          break;
        }
      }
    }
 }
</script>


